import React from 'react'

const ImportItenData = React.lazy(() => import('./views/quoteToCash/ImportItemData'))
const QuotationList = React.lazy(() => import('./views/quoteToCash/QuotationList'))
const NewQuotation = React.lazy(() => import('./views/quoteToCash/Quotation'))
const PdfContainer = React.lazy(() => import('./views/quoteToCash/PdfContainer'))

const routes = [
  { path: '/', exact: true, name: 'Home' },
  { path: '/quotation/index', name: '報價單列表', element: QuotationList },
  { path: '/quotation/:childPath', name: '報價單列表', element: NewQuotation },
  { path: '/import_item_data', name: 'Excel匯入報價項目', element: ImportItenData },
  { path: '/pdfContainer', name: 'pdf', element: PdfContainer },
]

export default routes
